@tailwind base;
@tailwind components;
@tailwind utilities;

/* Variables personalizadas */
:root {
  --primary: #2e7d32;
  --secondary: #558b2f;
  --light: #f1f8e9;
  --dark: #1b5e20;
  --accent: #ffeb3b;
}

/* Estilos generales */
html, body {
  scroll-behavior: smooth;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  overflow-x: hidden;
}

/* Animaciones */
@keyframes fadeInDown {
  from { opacity: 0; transform: translateY(-30px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideDown {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

.animate-fadeInDown {
  animation: fadeInDown 0.8s ease-out;
}

.animate-fadeInUp {
  animation: fadeInUp 0.8s ease-out;
}

.animate-fadeIn {
  animation: fadeIn 0.8s ease-out;
}

.animate-slideDown {
  animation: slideDown 0.3s ease-out;
}

/* Clases para elementos animados al hacer scroll */
.animate-on-scroll {
  opacity: 0;
  transition: all 0.6s ease-out;
  transform: translateY(20px);
}

.animate-on-scroll.animate-fadeIn {
  opacity: 1;
  transform: translateY(0);
}

@layer components {
  .btn-primary {
    @apply bg-green-700 hover:bg-green-800 text-white font-bold py-2 px-6 rounded-full transition transform hover:scale-105;
  }

  .btn-secondary {
    @apply bg-yellow-400 hover:bg-yellow-500 text-green-900 font-bold py-2 px-6 rounded-full transition transform hover:scale-105;
  }

  .section-padding {
    @apply py-16 md:py-24;
  }
}